<template>
  <div class="container">
    <div class="container__slider">
      <el-carousel height="350px" autoplay>
        <el-carousel-item v-for="item in MENUS" :key="item.src">
          <img
            @click="handleClickImg(item.path)"
            :src="item.src"
            :alt="item.name + '-img'"
            style="cursor: pointer"
          />
        </el-carousel-item>
      </el-carousel>
    </div>
    <div class="container__bread">
      <div class="container__bread__content">
        当前位置：
        <el-breadcrumb separator=">">
          <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
          <el-breadcrumb-item :to="{ path: '/products' }"
            >产品及解决方案</el-breadcrumb-item
          >
          <el-breadcrumb-item>{{ route.query.name }}</el-breadcrumb-item>
        </el-breadcrumb>
      </div>
    </div>
    <div class="container__content">
      <el-menu
        default-active="/products?name=166九栅半片组件"
        class="container__content__menu"
        @open="handleOpen"
        router
      >
        <el-submenu index="1">
          <template #title>
            <span>产品及解决方案</span>
          </template>
          <el-menu-item-group>
            <el-menu-item index="/products?name=166九栅半片组件"
              >166九栅半片组件</el-menu-item
            >
            <el-menu-item index="/products?name=常规单晶组件"
              >常规单晶组件</el-menu-item
            >
            <el-menu-item index="/products?name=常规多晶组件"
              >常规多晶组件</el-menu-item
            >
          </el-menu-item-group>
        </el-submenu>
      </el-menu>
      <div class="container__solutions">
        <div class="container__solutions__title">产品及解决方案</div>
        <div class="container__solutions__content">
          <template v-if="!route.query.img">
            <h4>166九栅半片组件</h4>
            <a href="/#/products" style="text-decoration: none"
              >166九栅半片组件</a
            >
            <h4>常规单晶组件</h4>
            <a
              href="/#/products?name=常规单晶组件&&img=true"
              style="text-decoration: none"
              >GPNE-S60 320W-300W
            </a>
            <h4>常规多晶组件</h4>
            <a
              href="/#/products?name=常规多晶组件&&img=true"
              style="text-decoration: none"
              >GPNE-P60 290W-270W
            </a>
          </template>
          <template v-else-if="route.query.name === '常规单晶组件'">
            <img
              src="http://www.gppv-energy.com/upLoad/image/20160503/14622558362466230.jpg"
              alt=""
            />
            <img
              src="http://www.gppv-energy.com/upLoad/image/20160503/14622558365978147.jpg"
              alt=""
            />
          </template>
          <template v-else-if="route.query.name === '常规多晶组件'">
            <img
              src="http://www.gppv-energy.com/upLoad/image/20160503/14622546378582731.jpg"
              alt=""
            />
            <img
              src="http://www.gppv-energy.com/upLoad/image/20160503/14622546373655500.jpg"
              alt=""
            />
          </template>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { useRoute } from 'vue-router'
import { useRouter } from 'vue-router'
export default {
  components: {},

  setup() {
    const route = useRoute()
    const router = useRouter()
    const MENUS = [
      {
        name: 'products',
        src: 'http://www.gppv-energy.com/Templates/cn/images/01.jpg',
        path: '/products?name=166九栅半片组件'
      },
      {
        name: 'about',
        src: 'http://www.gppv-energy.com/Templates/cn/images/02.jpg',
        path: '/about?name=企业简介'
      }
    ]
    const handleClickImg = path => {
      router.push(path)
    }
    const handleOpen = (key, keyPath) => {
      console.log(key, keyPath)
    }
    return {
      MENUS,
      route,
      handleClickImg,
      handleOpen
    }
  }
}
</script>

