    background: #f8f8f8;
    display: flex;
    align-items: center;
    &__content {
      width: 1200px;
      margin: 0 auto;
      display: flex;
      flex-direction: row;
      font-size: 14px;
    }
  }
  &__content {
    width: 1200px;
    height: auto;
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    /deep/ .el-menu {
      border-right: transparent;
